<template>
<div>
  <div class="info_info">
      <div class="info_base">
      <section class="person_info_base">
        <span>姓名: <strong>{{name}}</strong></span>
        <span>电话: <strong>{{tel}}</strong></span>
        <span>服务人员名称: <strong>{{serverName}}</strong></span>
        <span>服务时间: <strong>{{date}}</strong> <strong>{{time}}</strong></span>
      </section>
      <div class="address">
        <span style="padding: 0 0.2rem;display:inline-block;">备注: <strong>{{remark}}</strong></span>
      </div>
    </div>
    <div class="goods_list">
      <section class="list_header">
        <span class="item">订单号: <strong>{{order}}</strong></span>
        <span class="item">订单状态: <strong>{{state}}</strong></span>
      </section>
    </div>
    <ul class="goods">
      <li style="display: flex;justify-content: space-between;align-items: center">
        <div class="left">
          <img :src="pic" alt="暂无图片信息" class="photo_img" />
          <p class="left_info">
            <span>{{server}}</span>
            <span><strong>x1</strong> <strong style="display: inline-block;margin-left: 0.1rem">{{serverName}}</strong></span>
            <span>¥{{price}}</span>
          </p>
        </div>
        <div class="right"  v-if="this.state=== '待处理'">
          
          <Button type="info" @click.native="send">确定</Button>
          <Button type="warning" @click.native="refund">取消</Button>
        </div>
        <div class="right" v-if="this.state === '已确定'">
           
          <Button type="info" @click.native="finish">确定</Button>
        </div>
        <!--<div class="right">-->
            <!---->
        <!--</div>-->
      </li>
    </ul>
  </div>
</div>
</template>
<script>
export default{
    data(){
        return {
            name:"",
            tel:'',
            server:'',
            date:"",
            time:"",
            id:"",
            address:"",
            order:"",
            state:"",
            remark:'',
            price:"",
            pic:'',
            serverName:'',
            goodsList:[],
            order_id:'',
            goodsOrder:''
        }
    },
  filters:{
    checkState(val){
      switch(val){
        case 0 :
          return "待处理";
            break;
        case 1 :
          return "已确定";
            break;
        case 2 :
          return "已取消";
            break;
        case 3 :
          return "已完成";
            break;
      }
    }
  },
  methods:{
//    封装
    handelOrder(state,order){
        this.post("custom/Order/updateSubscribeOrderState",{
          state:state,
          order_id:this.order_id,
        }).then(res=>{
          if(res.data.code===10000){
            this.$Modal.success({
              title: "成功",
              content: "成功更改"
            });
            this.$router.go(-1);
          }else{
            this.$Modal.error({
              title: "修改失败",
              content: res.data.msg_test
            });
          }
        })
    },
    finish(){
      this.handelOrder(3,this.goodsOrder)
    },
    send(item){
      this.handelOrder(1,this.goodsOrder)
    },
    refund(item){
      this.handelOrder(2)
    }
  },
  activated(){
    this.$store.commit('setStatus',false);
    this.order_id= this.$route.params.id;
    this.post("custom/Order/getSubscribeOrderById",{
        order_id:this.order_id
    }).then(res=>{
        if(res.data.code===10000){
          this.name = res.data.data.username;
          this.tel = res.data.data.tel;
          this.server = res.data.data.subscribe_name ;
          this.date = res.data.data.subscribe_day ;
          this.time = new Date(res.data.data.subscribe_time * 1000).toLocaleString();
          this.order = res.data.data.order_sn;
          this.state = res.data.data.state;
          this.remark = res.data.data.remark;
          this.price = res.data.data.price;
          this.serverName = res.data.data.name;
          this.pic = "http://weapp.xiguawenhua.com/"+res.data.data.pic;
          if(this.state == 1){
              this.state="已确定";
          }else if(this.state == 0){
              this.state="待处理";
          }else if(this.state == 2){
              this.state="已取消";
          }else if(this.state == 3){
            this.state="已完成";
          }
        }else{
          this.$Modal.error({
            title: "修改失败",
            content: res.data.msg_test
          });
        }
    })
  }
}
</script>
<style scoped>
  .top {
    padding: 0.1rem 0;
    border-bottom: 1px solid #ebecee
  }

  header {
    display: flex;
    align-items: center;
  }

  header>span {
    font-size: 0.18rem;
    display: inline-block;
    margin-left: 0.1rem
  }

  p {
    padding: 0.1rem 0;
    font-size: 0.14rem;
    letter-spacing: 0.01rem
  }

  .info {
    padding: 0.1rem 0;
  }

  .person_info_base {
    background-color: white;
    padding: 0.2rem 0.2rem;
    border-bottom: 1px solid #EEEEEE;
  }

  .person_info_base>span {
    display: inline-block;
    padding: 0 1rem;
  }

  .person_info_base>span>strong {
    display: inline-block;
    margin-left: 0.05rem
  }

  .person_info_base>span:nth-child(1) {
    display: inline-block;
    padding: 0 0.2rem;
  }

  .address {
    padding: 0.2rem 0.2rem;
    margin-top: 0.01rem
  }

  .goods_list {
    margin-top: 0.2rem
  }

  .list_header {
    padding: 0.2rem 0.2rem;
    background-color: #F9FAFB;
    display: flex;
    justify-content: space-between
  }

  .item {
    color: #4E4E4E;
    padding: 0 0.2rem
  }

  .goods {
    padding: 0.2rem 0.2rem;
    border: 1px solid #f3f3f3;
    background: white;
  }

  .photo_img {
    width: 0.88rem;
    max-height: 0.8rem;
    margin: 0 0.2rem
  }

  .left {
    display: flex;
    justify-content: flex-start;
    align-items: center
  }

  .left_info {
    display: flex;
    flex-direction: column;
    margin-left: 0.1rem
  }

  .left_info>span {
    display: inline-block;
    padding: 0.02rem 0;
  }

  .right {
    padding: 0 0.2rem
  }

  .fahuo {
    margin-top: 20px;
    float: right;
    margin-right: 40px;
  }
  .info_base{
    background: white;
    box-shadow: 5px 5px 10px #E7E7E7;
  }
</style>



